body {
    user-select: none;
    font-family: "Roboto", sans-serif;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.header {
    position: fixed;
    width: 100%;
    z-index: 228;
    background-color: #032d615a;
    height: 6vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15%;
    color: rgb(255, 255, 255);
}

.headertext {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 30%;
}

.maintext {
    color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40vh;
}

.maintext>h1 {
    font-size: 45pt;
}

.maintext>h3 {
    font-size: 25pt;
}

.maintext>h6 {
    font-size: 10pt;
    margin-bottom: 2%;
    margin-top: 1%;
}

.maintext>button {
    color: rgb(255, 255, 255);
    background-color: #3885812f;
    border: 2px solid white;
    height: 6vh;
    width: 10%;
    transition: transform 0.3s;
}

.maintext>button:hover {
    cursor: pointer;
    transform: scale(1.1);
}

.backgroundImage {
    display: block;
    background-image: url("https://raw.githubusercontent.com/bandosiki/bandosiki.github.io/main/%D0%BA%D0%B8%D0%BD%D0%BE/background.jpg");
    background-size: cover;
    width: 100%;
    height: 100vh;
}

.content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 1%;
}

.spacebtn {
    border: none;
    background-color: #388581;
    border-radius: 10px;
    width: 15%;
    height: 0.3vh;
    margin-top: 1%;
}
.cards {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-left: 15%;
    padding-right: 15%;
    padding-top: 4%;
  }
.card {
    width: 20rem;
    height: 30rem;
    border-radius: 10px;
    cursor: pointer;
    position: relative;
    color: rgb(240, 240, 240);
    box-shadow: 0 10px 30px 5px rgba(0, 0, 0, 0.2);
    img {
        position: absolute;
        object-fit: cover;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        opacity: 0.9;
        transition: opacity 0.2s ease-out;
    }

    h2 {
        position: absolute;
        inset: auto auto 30px 30px;
        margin: 0;
        transition: inset 0.3s 0.3s ease-out;
        font-family: "Roboto Condensed", sans-serif;
        font-weight: normal;
        text-transform: uppercase;
    }

    p,
    a {
        position: absolute;
        opacity: 0;
        max-width: 80%;
        transition: opacity 0.3s ease-out;
    }

    p {
        inset: auto auto 80px 30px;
    }

    a {
        inset: auto auto 40px 30px;
        color: inherit;
        text-decoration: none;
    }

    &:hover h2 {
        inset: auto auto 220px 30px;
        transition: inset 0.3s ease-out;
    }

    &:hover p,
    &:hover a {
        opacity: 1;
        transition: opacity 0.5s 0.1s ease-in;
    }

    &:hover img {
        transition: opacity 0.3s ease-in;
        opacity: 1;
    }
}
.cards_circles {
    display: flex;
    flex-wrap: wrap;
    gap: 3rem;
    padding-top: 5%;
    padding-left: 18%;
    padding-right: 18%;
    justify-content: center;
  }
  
  .cards_circles > div {
    width: 30%; 
    border-radius: 20px;
    text-align: center;
  }
  .cards_circles img {
    width: 100%; 
    height: auto; 
    border-radius: 20px;
  }
.cards_squares {
    display: flex;
    flex-wrap: wrap;
    gap: 3rem;
    padding-top: 5%;
    padding-left: 18%;
    padding-right: 18%;
    justify-content: center;
  }
  
  .cards_squares > div {
    width: 30%; 
    border-radius: 20px;
    text-align: center;
  }
  .cards_squares img {
    width: 100%; 
    height: auto; 
    border-radius: 20px;
  }
.features{
    border-top: 1px solid black;
    margin-top: 3%;
    background-color: #DAE8E7;
}
.order{
    padding-top: 3%;
}
.ordertext{
    text-align: center;
}
.centerline{
    display: flex;
    justify-content: center;
}

.order_circles_list {
    display: flex;
    justify-content: center;
  }
  .step {
    margin-right: 20px; /* Adjust the space between steps */
    text-align: center;
    display: flex;
    width: 10%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .circle {
    width: 100px; /* Adjust the size of the circle */
    height: 100px;
    background-color: #000000; /* Adjust the background color of the circle */
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .icon {
    color: white; /* Adjust the color of icons */
    font-size: 36px; /* Adjust the size of icons */
  }